import {
  createStore
} from "vuex";
import axios from "axios";
export default createStore({
  state: {
    user: null,
    repos: null,
    object: null,
    url: 'https://api.github.com/users/'
  },
  mutations: {
    getUsers(state, payload) {
      state.user = payload;
    },
    getRepos(state, payload){
      state.repos = payload;
    }
  },
  actions: {
    async getObject({
      state,
      commit
    }, searchName) {
      if (searchName.length > 0) {
        try {
          let response = await axios.get(`${state.url}${searchName}`);
          commit("getUsers", response.data);
          let repos = await axios.get(`${state.url}${searchName}/repos`);
          commit("getRepos", repos.data);
        } catch (error) {
          console.error("Ошибка в запросе" + error);
        }
      }
    },
  },

  getters: {},
});

// https://api.github.com/users