<template >
    <div class="star">
        <div class="star__info">Сортировка</div>
        <div class="star__info-title">
            <button class="star__info-bg active" @click="sortedByAlpha">ИМЯ</button>
            <button class="star__info-bg active" @click="sortedByStars">ЗВЕЗДЫ</button>
            <button class="star__info-bg active" @click="sortDate">ДАТА</button>
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";
export default {
    computed: {
        ...mapState(["repos"]),
        
    },
    methods: {
        sortedByAlpha() {
            this.repos.sort((a, b) => {
                return a.name - b.name
            })
        },
        sortedByAlpha() {
            this.repos.sort((a, b) => a.name.localeCompare(b.name))
   
        },
        sortedByStars() {
            this.repos.sort((a, b) => {
                return b.stargazers_count - a.stargazers_count
            })
            
        },
        sortDate(a,b) {
            this.repos.sort((a, b) => b.created_at.localeCompare(a.created_at))
            }

        }

    }
    

</script>
