<template>
  <div class="search">
    <!-- //searchName// -->
    <input type="text" v-model="searchName" class="search__main" placeholder="Введите имя пользователя" />
    <button class="search__btn" @click="change(searchName)">НАЙТИ</button>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";

/* export {name} ; */
export default {
  data() {
    return {
      userInfo: [],
      searchName: ''
    };
  },
  methods: {
    // searcher(){
    //   this.$store.dispatch("getObject", this.searchName);
    // }
    ...mapActions(["getObject"]),
    ...mapMutations(["getUsers"]),
    change(name) {
      this.getObject(name);
      // console.log(this.getUser);
    },
  },
  computed: {
    // products() {
    //   return this.$store.state.users;
    // },
    ...mapState(["user"]),
    getUser: {
      get() {
        return this.user;
      },
      set(val) {
        this.getUsers(val);
      },
    },
  },
  mounted() { },
  created() {
    window.addEventListener('keypress', (e) => {
      if (e.key == 'Enter') {
        this.change()
      }
    })
  },
};
</script>
