<template lang="">
    <div class="info" v-if="user">
        <div class="info__left">
            <img 
            :src="user.avatar_url" alt="">
            <a target="_blank" :href="user.html_url" class="info__left-btn">ПОСЕТИТЬ</a>
        </div>
        <div class="info__right">
            <p class="info__right-title">{{ user.login}}</p>
            <p class="info__right-title">Репозиториев: {{ user.public_repos
}}</p>
            <p class="info__right-title">Создан:  {{ user.created_at.slice(0,10)}}</p>
            <p class="info__right-title">Подписщиков: {{ user.followers}}</p>
            <p class="info__right-title">Подписок:  {{ user.following}}</p>
        </div>
    </div>
</template>
<script>

import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["user"]),
  },
  methods: {
    site(){
      this.user
    }
  },
};
</script>
